/// <amd-module name='PromiseSeries'/>
///<reference path="bluebird.d.ts"/>
export var series = function(promiseArr) {
    alert(JSON.stringify(Promise.reduce));
    return Promise.reduce(promiseArr, (values, promise: any) => {
        return promise().then((result) => {
            values.push(result);
            return values;
        });
    }, []);
};
